@import 'root.css';


.clickable  {
    color: var(--primary-text);
    cursor: pointer;
    -webkit-user-select: none;
}

.open > :first-child { color: var(--secondary); }


/* Button Icon */
.icon-button {
    --button-size: calc(var(--nav-size) * 0.5);

    width: var(--button-size);
    height: var(--button-size);
    padding: 5px;
    margin: 2px;
    
    border-radius: 50%;
    background-color: var(--primary);
    
    display: flex;
    align-items: center;
    justify-content: center;
}

.icon-button:hover { filter: var(--hover); }
.icon-button:active { transform: scale(0.95); }


/* Menus */
.navbar {
    position: absolute;
    left: 0px;
    top: 0px;

    width: calc(100vw - 2rem);
    height: var(--nav-size);
    padding: 0 1rem;

    background-color: var(--primary-dark);
    border-bottom: var(--border);
    box-shadow: var(--shadow);
}

.dropdown {
    position: absolute;
    top: calc(var(--nav-size) - 2px);
    transform: translateX(-45%);

    min-width: 300px;
    padding: 1rem;
    
    border-radius: var(--border-radius);
    background-color: var(--primary-dark);
    border: var(--border);
    box-shadow: var(--shadow);
    
    overflow: hidden;
}

.sidebar {
    position: absolute;
    left: 0px;
    top: var(--nav-size);
    transform: translateX(-105%);
    
    width: 300px;
    height: calc(100vh - 2rem);
    padding: 1rem 0rem;
    
    background-color: var(--primary-dark);
    border-right: var(--border);
    border-bottom: var(--border);
    box-shadow: var(--shadow);
}

.sidebar-exit-active { transform: translateX(-105%); }
.sidebar-enter-active, .sidebar-enter-done { transform: translateX(0); }


/* Menu Items */
.nav-item {
    position: relative;
    width: calc(var(--nav-size) * 0.8);

    display: flex;
    align-items: center;
    justify-content: center;
}

.drop-item {
    height: 50px;
    padding: 0.5rem;

    border-radius: var(--border-radius);
    background-color: var(--primary-dark);
    
    display: flex;
    align-items: center;
}

.drop-item:hover { color: var(--secondary); filter: var(--hover); }
.drop-item:active { filter: var(--active); }

.side-item {
    height: 50px;
    padding: 0.5rem 1.5rem;

    background-color: var(--primary-dark);

    display: flex;
    align-items: center;
}

.side-item:hover {
    transform: translateX(5%) scale(1.05);

    color: var(--secondary);
    border-radius: var(--border-radius);
    box-shadow: var(--shadow);
    filter: var(--hover);

    z-index: 1;
}
.side-item:active { transform: translateX(5%); box-shadow: none; }


/* Menu Item Layout */
.drop-item > .icon-button, .side-item > .icon-button { margin-right: 10px; }
.drop-item > .icon-button:hover, .side-item > .icon-button:hover { filter: none; }
.icon-right { margin-left: auto; }

/* Other */
.navbar-nav {
    max-width: 100%;
    height: 100%;

    display: flex;
    justify-content: flex-end;
}

.sidebar-nav {
    width: 100%;
    max-height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.user-image {
    --button-size: calc(var(--nav-size) * 0.5 + 10px);

    width: var(--button-size);
    height: var(--button-size);

    border-radius: 50%;
}

.nav-badge {
    position: absolute;
    top: 0px;
    right: -5px;
    width: 1rem;
    height: 1rem;
    padding: 3px;
    font-size: 0.8rem;
    
    color: var(--secondary-text);
    border-radius: 50%;
    background-color: var(--secondary);
    font-weight: bold;

    text-align: center;
}


/* Main Content */
.main {
    width: 100%;
    height: calc(100% - var(--nav-size));
    margin-top: var(--nav-size);

    color: var(--primary-text);
    background-color: var(--primary);

    display: flex;
    justify-content: center;
}

.main-container {
    width: 1000px;
    height: calc(100% - 2rem);
    padding: 1rem;
    
    background-color: var(--primary-dark);
    box-shadow: var(--shadow);
}