@import url(https://fonts.googleapis.com/css?family=Material+Icons|Material+Icons+Outlined|Material+Icons+Two+Tone|Material+Icons+Round|Material+Icons+Sharp);
:root{
    --primary: #eceff1;
    --primary-dark: #ffffff;
    --primary-light: #babdbe;
    --primary-text: #000000;
    --secondary: #2196f3;
    --secondary-dark: #6ec6ff;
    --secondary-light: #0069c0;
    --secondary-text: #ffffff;

    --border: 1px solid var(--primary);
    --hover: brightness(0.93);
    --active: brightness(0.85);
    --shadow: rgba(0, 0, 0, 0.05) 0px 3px 7px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px;


    --speed: 200ms;
    --speed2: 0.6s;
}

@media (prefers-color-scheme: dark) {
    :root{
        --primary: #424446;
        --primary-light: #6d7175;
        --primary-dark: #1b1c1d;
        --primary-text: #dadce1;
        --secondary: #2962ff;
        --secondary-light: #768fff;
        --secondary-dark: #0039cb;
        --secondary-text: #dadce1;

        --border: 1px solid var(--primary);
        --hover: brightness(1.4);
        --active: brightness(0.9);
        --shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px;
    }
}

@media (prefers-reduced-motion: reduce) {
    :root {
        --speed: 0;
        --speed2: 0;
    }
}

:root {
    --nav-size: 60px;
    --border-radius: 8px;
}

*:not(body) { transition: var(--speed), color calc(var(--speed) / 2); }
*:active { transition: 0s; }

ul {
    list-style: none;
    margin: 0px;
    padding: 0px;
}

body, #root, .root {
    margin: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow: hidden;
    width: 100vw;
    height: 100vh;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}


/* Loading Component */
.loading-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}
.lds-ellipsis div {
    position: absolute;
    top: 33px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: var(--primary-text);
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
            animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

@-webkit-keyframes lds-ellipsis1 { from { transform: scale(0); } to { transform: scale(1); } }

@keyframes lds-ellipsis1 { from { transform: scale(0); } to { transform: scale(1); } }
.lds-ellipsis div:nth-child(1) { left: 8px; -webkit-animation: lds-ellipsis1 var(--speed2) infinite; animation: lds-ellipsis1 var(--speed2) infinite; }

@-webkit-keyframes lds-ellipsis3 { from { transform: scale(1); } to { transform: scale(0); } }

@keyframes lds-ellipsis3 { from { transform: scale(1); } to { transform: scale(0); } }
.lds-ellipsis div:nth-child(2) { left: 8px; -webkit-animation: lds-ellipsis2 var(--speed2) infinite; animation: lds-ellipsis2 var(--speed2) infinite; }
.lds-ellipsis div:nth-child(3) { left: 32px; -webkit-animation: lds-ellipsis2 var(--speed2) infinite; animation: lds-ellipsis2 var(--speed2) infinite; }

@-webkit-keyframes lds-ellipsis2 { from { transform: translate(0, 0); } to { transform: translate(24px, 0); } }

@keyframes lds-ellipsis2 { from { transform: translate(0, 0); } to { transform: translate(24px, 0); } }
.lds-ellipsis div:nth-child(4) { left: 56px; -webkit-animation: lds-ellipsis3 var(--speed2) infinite; animation: lds-ellipsis3 var(--speed2) infinite; }

/* Switch Component */
.switch {
    width: 60px;
    height: 30px;
    
    border-radius: 100px;
    background-color: var(--primary);
}

.switch:after {
    display: block;
    content: '';
    position: relative;
    top: 5px;

    width: 20px;
    height: 20px;

    border-radius: 50%;

    transition: var(--speed);
}

.switch.off:after { left: 5px; background-color: var(--primary-dark); }
.switch.on:after { left: 35px; background-color: var(--secondary); }
:root {
    --image-size: 30px;
    --image-margin: 5px;
    --font-size: 1rem;
    --text-margin: calc(var(--font-size) * 0.45);
    --text-padding: 0.5rem;
    --button-radius: calc(var(--font-size) + 2 * var(--text-padding));
}

.msg-image {
    width: 30px;
    width: var(--image-size);
    height: 30px;
    height: var(--image-size);
    margin: 5px;
    margin: var(--image-margin);
    font-size: 30px;
    font-size: var(--image-size);

    border-radius: 50%;
    color: var(--secondary);

    cursor: default;
    -webkit-user-select: none;
}

/* Main Chat */
.chat {
    max-width: 100%;
    height: 100%;

    border-radius: var(--border-radius);

    display: flex;
    flex-direction: column;

    overflow: hidden;
}

.chat-header {
    font-size: 2em;
    padding: 0 0 0.5rem 0.5rem;
    padding: 0 0 var(--text-padding) var(--text-padding);

    font-weight: bold;
}

.chat-body {
    flex: auto;
    max-width: 100%;
    height: 100%;
    padding: 0.5rem 0;
    padding: var(--text-padding) 0;

    overflow: scroll;
}
.chat-body * { transition: 0s; }

.chat-footer {
    max-width: 100%;
    min-height: calc(30px + 2 * 5px);
    min-height: calc(var(--image-size) + 2 * var(--image-margin));
    padding-top: 0.5rem;
    padding-top: var(--text-padding);

    display: flex;
    align-items: center;
}

/* Input Bar */
.chat-input {
    flex: auto;
    padding: 0.5rem calc(2 * 0.5rem);
    padding: var(--text-padding) calc(2 * var(--text-padding));
    font-size: 1rem;
    font-size: var(--font-size);
    margin: 1px;
    margin-right: 0;

    color: var(--secondary-text);
    border-radius: calc(1rem + 2 * 0.5rem) 0 0 calc(1rem + 2 * 0.5rem);
    border-radius: var(--button-radius) 0 0 var(--button-radius);
    background-color: var(--primary);
    border: 2px solid var(--primary);
    
    outline: none;
}

.chat-input:hover { -webkit-filter: var(--hover); filter: var(--hover); }
.chat-input:focus { border-color: var(--secondary); }

.chat-send {
    width: 100px;
    padding: 0.5rem;
    padding: var(--text-padding);
    font-size: 1rem;
    font-size: var(--font-size);
    margin: 1px;
    margin-left: 0;
    
    color: var(--secondary-text);
    border-radius: 0 calc(1rem + 2 * 0.5rem) calc(1rem + 2 * 0.5rem) 0;
    border-radius: 0 var(--button-radius) var(--button-radius) 0;
    background-color: var(--secondary);
    border: 2px solid var(--secondary);
    font-feature-settings: "smcp", "c2sc";
    font-variant-caps: all-small-caps;
    font-weight: bold;

    cursor: pointer;
}

.chat-send:hover { -webkit-filter: var(--hover); filter: var(--hover); }
.chat-send:active { -webkit-filter: var(--active); filter: var(--active); }

/* Message */
.message {
    max-width: 100%;
    min-height: calc(30px + 2 * 5px);
    min-height: calc(var(--image-size) + 2 * var(--image-margin));

    display: flex;
    align-items: center;
}

.msg-text {
    padding: calc(1rem * 0.45) calc(calc(1rem * 0.45) * 1.5);
    padding: var(--text-margin) calc(var(--text-margin) * 1.5);

    color: var(--primary-text);
    border-radius: 1rem;
    border-radius: var(--font-size);
    background-color: var(--primary);

    cursor: default;
    -webkit-user-select: none;
}

.sent { flex-direction: row-reverse; }
.sent > .msg-text { background-color: var(--secondary); color: var(--secondary-text) }

.connect-top { margin-top: calc(1.2px - calc(1rem * 0.45)); margin-top: calc(1.2px - var(--text-margin)); }

.sent.connect-top { margin-right: 40px; }
.sent.connect-top > .msg-text { border-top-right-radius: calc(var(--border-radius) / 2); }
.sent.connect-bottom > .msg-text { border-bottom-right-radius: calc(var(--border-radius) / 2); }

.received.connect-top { margin-left: 40px; }
.received.connect-top > .msg-text { border-top-left-radius: calc(var(--border-radius) / 2); }
.received.connect-bottom > .msg-text { border-bottom-left-radius: calc(var(--border-radius) / 2); }
.clickable  {
    color: var(--primary-text);
    cursor: pointer;
    -webkit-user-select: none;
}

.open > :first-child { color: var(--secondary); }


/* Button Icon */
.icon-button {
    --button-size: calc(var(--nav-size) * 0.5);

    width: var(--button-size);
    height: var(--button-size);
    padding: 5px;
    margin: 2px;
    
    border-radius: 50%;
    background-color: var(--primary);
    
    display: flex;
    align-items: center;
    justify-content: center;
}

.icon-button:hover { -webkit-filter: var(--hover); filter: var(--hover); }
.icon-button:active { transform: scale(0.95); }


/* Menus */
.navbar {
    position: absolute;
    left: 0px;
    top: 0px;

    width: calc(100vw - 2rem);
    height: var(--nav-size);
    padding: 0 1rem;

    background-color: var(--primary-dark);
    border-bottom: var(--border);
    box-shadow: var(--shadow);
}

.dropdown {
    position: absolute;
    top: calc(var(--nav-size) - 2px);
    transform: translateX(-45%);

    min-width: 300px;
    padding: 1rem;
    
    border-radius: var(--border-radius);
    background-color: var(--primary-dark);
    border: var(--border);
    box-shadow: var(--shadow);
    
    overflow: hidden;
}

.sidebar {
    position: absolute;
    left: 0px;
    top: var(--nav-size);
    transform: translateX(-105%);
    
    width: 300px;
    height: calc(100vh - 2rem);
    padding: 1rem 0rem;
    
    background-color: var(--primary-dark);
    border-right: var(--border);
    border-bottom: var(--border);
    box-shadow: var(--shadow);
}

.sidebar-exit-active { transform: translateX(-105%); }
.sidebar-enter-active, .sidebar-enter-done { transform: translateX(0); }


/* Menu Items */
.nav-item {
    position: relative;
    width: calc(var(--nav-size) * 0.8);

    display: flex;
    align-items: center;
    justify-content: center;
}

.drop-item {
    height: 50px;
    padding: 0.5rem;

    border-radius: var(--border-radius);
    background-color: var(--primary-dark);
    
    display: flex;
    align-items: center;
}

.drop-item:hover { color: var(--secondary); -webkit-filter: var(--hover); filter: var(--hover); }
.drop-item:active { -webkit-filter: var(--active); filter: var(--active); }

.side-item {
    height: 50px;
    padding: 0.5rem 1.5rem;

    background-color: var(--primary-dark);

    display: flex;
    align-items: center;
}

.side-item:hover {
    transform: translateX(5%) scale(1.05);

    color: var(--secondary);
    border-radius: var(--border-radius);
    box-shadow: var(--shadow);
    -webkit-filter: var(--hover);
            filter: var(--hover);

    z-index: 1;
}
.side-item:active { transform: translateX(5%); box-shadow: none; }


/* Menu Item Layout */
.drop-item > .icon-button, .side-item > .icon-button { margin-right: 10px; }
.drop-item > .icon-button:hover, .side-item > .icon-button:hover { -webkit-filter: none; filter: none; }
.icon-right { margin-left: auto; }

/* Other */
.navbar-nav {
    max-width: 100%;
    height: 100%;

    display: flex;
    justify-content: flex-end;
}

.sidebar-nav {
    width: 100%;
    max-height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.user-image {
    --button-size: calc(var(--nav-size) * 0.5 + 10px);

    width: var(--button-size);
    height: var(--button-size);

    border-radius: 50%;
}

.nav-badge {
    position: absolute;
    top: 0px;
    right: -5px;
    width: 1rem;
    height: 1rem;
    padding: 3px;
    font-size: 0.8rem;
    
    color: var(--secondary-text);
    border-radius: 50%;
    background-color: var(--secondary);
    font-weight: bold;

    text-align: center;
}


/* Main Content */
.main {
    width: 100%;
    height: calc(100% - var(--nav-size));
    margin-top: var(--nav-size);

    color: var(--primary-text);
    background-color: var(--primary);

    display: flex;
    justify-content: center;
}

.main-container {
    width: 1000px;
    height: calc(100% - 2rem);
    padding: 1rem;
    
    background-color: var(--primary-dark);
    box-shadow: var(--shadow);
}
