@import 'https://fonts.googleapis.com/css?family=Material+Icons|Material+Icons+Outlined|Material+Icons+Two+Tone|Material+Icons+Round|Material+Icons+Sharp';

@import 'accessibility.css';

:root {
    --nav-size: 60px;
    --border-radius: 8px;
}

*:not(body) { transition: var(--speed), color calc(var(--speed) / 2); }
*:active { transition: 0s; }

ul {
    list-style: none;
    margin: 0px;
    padding: 0px;
}

body, #root, .root {
    margin: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow: hidden;
    width: 100vw;
    height: 100vh;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}


/* Loading Component */
.loading-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}
.lds-ellipsis div {
    position: absolute;
    top: 33px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: var(--primary-text);
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

@keyframes lds-ellipsis1 { from { transform: scale(0); } to { transform: scale(1); } }
.lds-ellipsis div:nth-child(1) { left: 8px; animation: lds-ellipsis1 var(--speed2) infinite; }

@keyframes lds-ellipsis3 { from { transform: scale(1); } to { transform: scale(0); } }
.lds-ellipsis div:nth-child(2) { left: 8px; animation: lds-ellipsis2 var(--speed2) infinite; }
.lds-ellipsis div:nth-child(3) { left: 32px; animation: lds-ellipsis2 var(--speed2) infinite; }

@keyframes lds-ellipsis2 { from { transform: translate(0, 0); } to { transform: translate(24px, 0); } }
.lds-ellipsis div:nth-child(4) { left: 56px; animation: lds-ellipsis3 var(--speed2) infinite; }

/* Switch Component */
.switch {
    width: 60px;
    height: 30px;
    
    border-radius: 100px;
    background-color: var(--primary);
}

.switch:after {
    display: block;
    content: '';
    position: relative;
    top: 5px;

    width: 20px;
    height: 20px;

    border-radius: 50%;

    transition: var(--speed);
}

.switch.off:after { left: 5px; background-color: var(--primary-dark); }
.switch.on:after { left: 35px; background-color: var(--secondary); }